as colombianas, estas ofertas van desde la siembra y cosecha de la caña de azúcar, hasta la producción de la panela, todo esto es gracias al proceso de transformación del jugo de la caña en derivados, que constituyen alimentos básicos para los hogares y también para mover la economía de la región y del país. Las actividades que conducen a la fabricación de tal comestible, conllevan situaciones de riesgo para la salud humana y la integridad física, dado a la exposición a herramientas cortopunzantes, animales, fuego, elevadas temperaturas, vapores, entre otros factores para que las personas realicen las labores de forma adecuada; por ello es necesario que cada uno cuente con las condiciones necesarias para preservar la salud e integridad mientras trabajan, porque sin salud no hay trabajo, sin trabajo no hay producción y sin producción no hay ingresos (Cundinamarca, 2016). ")]),_c('p',[_vm._v("Por tal razón, es de obligatorio que los encargados de los trapiches tomen conciencia de ofrecer las condiciones mínimas, para la amparar la seguridad, la salud y la vida, custodiando que se apliquen las medidas y recomendaciones para lograrlo y así evitar enfermedades y accidentes, garantizando la producción, el bienestar de los productores, trabajadores y las familias. ")]),_c('p',[_vm._v("Dentro de los conceptos para tener en cuenta están: ")])]),_c('div',{staticClass:"col-md-5 col-lg-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/img-14.png")}})])])]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0 px-5 col-lg-8"},[_c('h3',[_vm._v("Salud laboral u ocupacional ")]),_c('p',[_vm._v("Hace referencia al bienestar del empleado, además busca protegerlo de posibles accidentes durante la ejecución de las labores cotidianas. Las tácticas se encaminan en propagar el trabajo seguro y sano para, contando con ambientes óptimos y un excelente lugar de convivencia, donde prevalezca la tranquilidad física, mental